/* eslint-disable no-undef */
import * as THREE from "three";
import { Scene } from "three";
import EventInput from "./eventInput";
import ProductManager from "../product";
import factory from "../lib/obj_factory";
// pass
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass';
import { CSS2DRenderer } from 'three/examples/jsm/renderers/CSS2DRenderer';
import environmentEffect from "./environmentEffect";
import Tunnel from "../obj/obj_tunnel";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { FlyControls } from "three/examples/jsm/controls/FlyControls";
import FLy from "./flyTo";
import { parkDefult } from "../lib/viewpoint";
import {initData} from "../lib/viewpoint";
class Meta {
    constructor() {
        this.scene = new Scene; //
        this.factory = factory;
        this.camera = null; //
        this.render = null; // 
        this.clock = new THREE.Clock(); // 
        this.CSS2DRenderer = new CSS2DRenderer(); // 2D
        this.isLogin = 0;
        this.dom = null;
        this.scId = null; //id
        this.productManager = new ProductManager;
        this.mixerObject = {};
        this.isFlyControls = false;
        this.isScreen = false;// 
        this.onLoad = false;
    }


    /**
 *  
 */
    async init(dom) {
        //  dom 
        this.dom = dom || document.getElementById('div3d');
        // 
        // 
        this.initSceneAndCamera();
        // 
        this.initRender();
        // top
        this.creatCSS2DRenderer();
        // 
        this.initControls();
        // this.scId = param.scid;
        this.tunnel = new Tunnel(this, () => {
            this.callFun();
            // 
            this.animate();
            // dom
            this.updateOnWindowResize();
            // this.tunnel.loadeffect();
        });
        this.scene.add(this.tunnel.group);
    }

    async loadScene() {
        // //  1.0
        await this.productManager.loadModels();
        // 
        await this.productManager.loadMaterials();
        // // 
        this.productManager.setParkObj(this.productManager.getModelByName('demo').scene);
        this.factory.getCis(10010, () => {
            //     // 
            const park = this.scene.children.filter((cur) => cur.type === 'Park')[0];
            if (park) {
                park.position.setY(208);
            }
            // this.effect();
            this.callFun();
            // 
            this.animate();
        });
    }
    /**
     * init 
     */
    initSceneAndCamera() {
        //
        this.camera = new THREE.PerspectiveCamera(45, this.dom.clientWidth / this.dom.clientHeight, 1, 5000000);
        this.camera.position.set(6706.452849809113, 3466.627800031605, 3438.7962564494514);
        this.camera.lookAt(this.scene.position);
        this.scene.add(this.camera);
    }
    /**
     * 
     */
    callFun() {
        this.effect = new environmentEffect(this);
        // 
        this.eventInput = new EventInput(this);
        // 
        this.fly = new FLy(this);
        // 
        // this.createFlyControls();
        // 
        // 
         // 
        initData(this.dom.offsetWidth > 1920 ? true : false);
        this.isScreen = this.dom.offsetWidth > 1920 ? false : false;
        this.onLoad = true;
        this.fly.flyTo('default', () => {
            parkDefult();
        })
    }

    /**
     *  render
     * @returns {Promise<*>}
     */
    initRender() {
        //
        const canvas = document.createElement('canvas');
        const context = canvas.getContext('webgl2', {
            antialias: true,
            alpha: true
        });
        this.render = new THREE.WebGLRenderer({ canvas: canvas, context: context }); // 
        this.render.gammaInput = true;
        // 
        this.render.shadowMap.type = THREE.PCFSoftShadowMap;
        this.render.gammaOutput = true;
        this.render.shadowMap.enabled = true;
        this.render.setSize(this.dom.clientWidth, this.dom.clientHeight);//
        // 
        this.composer = new EffectComposer(this.render);
        const renderPass = new RenderPass(this.scene, this.camera);
        this.composer.addPass(renderPass);
        this.dom.appendChild(this.render.domElement); //
    }

    /**
     * 2DRender
     */
    creatCSS2DRenderer() {
        this.CSS2DRenderer.setSize(this.dom.clientWidth, this.dom.clientHeight);
        this.CSS2DRenderer.domElement.style.position = 'absolute';
        this.CSS2DRenderer.domElement.style.top = '0';
        this.dom.appendChild(this.CSS2DRenderer.domElement);
    }

    /**
     * 
     */
    createFlyControls() {
        this.flyControls = new FlyControls(this.camera, this.render.domElement)
        this.flyControls.movementSpeed = 35 //
        this.flyControls.domElement = this.render.domElement //dom
        this.flyControls.rollSpeed = Math.PI / 6 //
        this.flyControls.autoForward = false //
        this.flyControls.dragToLook = false //
    }

    /**
     * 
     * @returns {*|exports.default|THREE.OrbitControls}
     */
    initControls() {
        const controls = new OrbitControls(this.camera, this.CSS2DRenderer.domElement);
        controls.target.set(0, 0.5, 0);
        // controls.enableDamping = true;
        //
        controls.enableZoom = true;
        //
        //controls.autoRotate = true;
        //
        controls.enablePan = true;
        //
        controls.maxDistance = 20000;
        this.render.controls = controls;
    }
    /**
     * 
     * update
     */
    renderer3dUpdate() {
        //
        if (this.mixer) {
            this.mixer.update(this.clock.getDelta())
        }
        // 
        // this.isFlyControls && this.flyControls.update(this.clock.getDelta());
        // 
        this.tunnel && this.tunnel.animate(this.clock.getDelta());

        // 
        this.fly.animate(this.clock.getDelta());
        // 2D
        this.CSS2DRenderer.render(this.scene, this.camera);
        // 
        this.composer.render();
    }

    /**
     * 
     * @param {*} name 
     * @param {*} mesh 
     */
    setMixerObj(name, mesh) {
        this.mixerObject[name] = mesh
    }

    /**
     * 
     * @param {*} name 
     * @param {*} mesh 
     */
    getMixerObj(name) {
        return this.mixerObject[name]
    }

    /**
     * 
     * @param {*} name 
     */
    removeMixer(name) {
        delete this.mixerObject[name];
    }

    /**
     * 
     * @param {*} time 
     */
    updateMixer(time) {
        for (let cur in this.mixerObject) {
            this.mixerObject[cur].update(time);
        }
    }


    /**
     * 
     */
    animate() {
        window.META.renderer3dUpdate();
        // 
        // if ( this.mixer ) this.mixer.update( dt );
        // 
        // stats.update();
        // 3D
        requestAnimationFrame(window.META.animate);//
        // META.render.render(META.scene, META.camera);
    }

    updateOnWindowResize() {
        window.addEventListener('resize', () => {
            this.updateDomResize();
        }, true);
    }

    updateDomResize() {
        let innerWidth = window.innerWidth;
        let innerHeight = window.innerHeight;
        this.dom.style.width = innerWidth + 'px';
        this.dom.style.height = innerHeight + 'px';
        this.camera.aspect = innerWidth / innerHeight;
        this.camera.updateProjectionMatrix();
        this.render.setSize(innerWidth, innerHeight);
    }

}
window.META = new Meta();